/**
 * This class provides utility methods.
 */
define(['q', 'scriptjs.min'], function(Q, $script) {
    "use strict";

    module.exports.loadLibrary = function(scriptUrl) {
        var deferred = Q.defer();
        $script(scriptUrl, function() {
            deferred.resolve();
        }, function() {
            // use JSON.stringify here because scriptUrl may be a string or an array of string.
            deferred.reject('error loading:' + JSON.stringify(scriptUrl));
        });
        return deferred.promise;
    };

    module.exports.buildTwitterShareUrl = function (shareLink, message) {
        return "https://twitter.com/intent/tweet?original_referer=" + shareLink + "&text=" + encodeURIComponent(message) + "&tw_p=tweetbutton&url=" + shareLink;
    };

    module.exports.buildFacebookShareUrl = function (shareLink) {
        return "https://www.facebook.com/sharer/sharer.php?u=" + shareLink;
    };
});


